.dropText {
  position: absolute;
  font-family: var(--Karla);
  font-size: 20px;
  right: 54px;
  top: 370px;
  text-align: right;
  font-weight: bold;
  /* animation: Trans 3s linear infinite; */
  color: rgba(0, 0, 0, 0.7);
}

@keyframes Trans {
  95% {
    right: 54px;
    opacity: 1;
  }
  100% {
    right: 250px;
    opacity: 0;
    visibility: hidden;
  }
}
