.logoContainer {
  /* border: 1px solid black; */
  display: flex;
  flex-direction: column;
  position: relative;
  width: 120px;
  top: 30px;
  left: 60vw;
  text-align: center;
  font-family: var(--karla);
}
.JS {
  height: 90px;
  width: 90px;
  color: #f0db4f;
  position: relative;
  margin-top: 30px;
}
.bar1 {
  height: 10px;
  width: 0px;
  /* border: 1px solid black; */
  position: absolute;
  left: 970px;
  bottom: 505px;
  border-radius: 20px;
  animation: increase 4s linear infinite forwards;
  background-color: rgba(221, 221, 221, 0.801);
}
.bar2 {
  height: 10px;
  width: 0px;
  /* border: 1px solid black; */
  position: absolute;
  left: 970px;
  bottom: 370px;
  border-radius: 20px;
  animation: increase2 4s linear infinite forwards;
  background-color: rgba(221, 221, 221, 0.801);
}
.bar3 {
  height: 10px;
  width: 0px;
  /* border: 1px solid black; */
  position: absolute;
  left: 970px;
  bottom: 235px;
  border-radius: 20px;
  animation: increase3 4s linear infinite forwards;
  background-color: rgba(221, 221, 221, 0.801);
}
.bar4 {
  height: 10px;
  width: 0px;
  /* border: 1px solid black; */
  position: absolute;
  left: 970px;
  bottom: 100px;
  border-radius: 20px;
  animation: increase4 4s linear infinite forwards;
  background-color: rgba(221, 221, 221, 0.801);
}

@keyframes increase {
  from {
    width: 0px;
  }
  10% {
    width: 380px;
  }
  to {
    width: 380px;
    background-color: #f0db4f;
  }
}

@keyframes increase2 {
  from {
    width: 0px;
  }
  20% {
    width: 420px;
  }
  to {
    width: 420px;
    background-color: #e44d26;
  }
}

@keyframes increase3 {
  from {
    width: 0px;
  }
  30% {
    width: 420px;
  }
  to {
    width: 420px;
    background-color: #264de4;
  }
}

@keyframes increase4 {
  from {
    width: 0px;
  }
  40% {
    width: 330px;
  }
  to {
    width: 330px;
    background-color: #61dbfb;
  }
}

.HTML {
  height: 90px;
  width: 90px;
  color: #e44d26;
  margin-top: 30px;
}
.CSS {
  height: 90px;
  width: 90px;
  color: #264de4;
  margin-top: 30px;
}
.React {
  height: 90px;
  width: 90px;
  color: #61dbfb;
  margin-top: 30px;
}
